"use strict";

const form_box = document.getElementById('form');
const now_crystals = document.getElementById('input_num');
const other_cristal = document.getElementById('other_cristal');
const inputDate = document.getElementById('input_date');

const redlotus = document.getElementById('redlotus');

const date = new Date();
let endDate = 0;

form_box.addEventListener('change', () => submitVal());
form_box.addEventListener('submit', (e) => {
    e.preventDefault();
    submitVal();
});

// Вывод значений
function submitVal () {
    let val = 0;
    let box = document.getElementById('result');

    endDate = new Date(inputDate.value);
    val = Number(now_crystals.value) + Number(other_cristal.value) + DailiesCrystals() + dailySignCrystals() + WeekArmadaCrystals() + MemorialArenaCrystals() + MounthCartCrystals() + AbyssCrystals(redlotus.value);

    box.innerHTML = '';
    box.innerHTML += `<div class="item">Кристаллы за ежедневные задания: ${DailiesCrystals()}</div>`;
    box.innerHTML += `<div class="item">Кристаллы за вход в игру: ${dailySignCrystals()}</div>`;
    box.innerHTML += `<div class="item">Еженедельные кристаллы от клана: ${WeekArmadaCrystals()}</div>`;
    box.innerHTML += `<div class="item">Кристаллы за еженедельных боссов: ${MemorialArenaCrystals()}</div>`;
    box.innerHTML += `<div class="item">Кристаллы за месячную карту: ${MounthCartCrystals()}</div>`;
    box.innerHTML += `<div class="item">Кристаллы за прохождение бездны: ${AbyssCrystals(redlotus.value)}</div>`;
    box.innerHTML += `<div class="item"><b>Всего: ${val}</b></div>`;
}

// Кристаллы за месячную карту
function MounthCartCrystals () {
    const CRYSTALS = 60;
    // Поправка на то что, покупаю карту толко первого числа
    let nonCrystals = () => {
        return whatMounthDay(endDate, 31) * CRYSTALS;
    };

    return countDays() * CRYSTALS + whatMounthDay(endDate, 1) * 330 + whatMounthDay(endDate, 15, 30) * 500 - nonCrystals();
}

// Кристаллы за ежедневное выполнение заданий
function DailiesCrystals () {
    const CRYSTALS = 40;

    return countDays() * CRYSTALS;
}

// Кристаллы за еженедельных боссов
function MemorialArenaCrystals () {
    const DAYS = [6, 0]; //Каждую субботу и воскресение
    const CRYSTALS = 50;

    return whatWeekDay(endDate, ...DAYS) * CRYSTALS;
}

// Кристаллы за прохождение бездны
function AbyssCrystals (redDay) {
    const DAYS = [1, 4]; //Каждый четверг и понедельник
    const CRYSTALS_GARANT = 100;
    const CRYSTALS_AGONY = 320;
    const CRYSTALS_REDLOTUS = 400;
    let colDay = whatWeekDay(endDate, ...DAYS);

    redDay = colDay < redDay ? colDay : redDay;
    let agonyDay = colDay - redDay;

    return colDay * CRYSTALS_GARANT + agonyDay * CRYSTALS_AGONY + redDay * CRYSTALS_REDLOTUS;
}

// Кристаллы за вход в игру
function dailySignCrystals () {
    const DAYS = [1, 6, 8, 13, 15, 20, 22, 29];
    const CRYSTALS = 50;

    return whatMounthDay(endDate, ...DAYS) * CRYSTALS;
}

// Еженедельные кристаллы от клана
function WeekArmadaCrystals () {
    const DAYS = 1; //Каждую понедельник
    const CRYSTALS = 25;

    return whatWeekDay(endDate, DAYS) * CRYSTALS;
}

// Считает количество дней
function countDays() {
    let days = endDate - date;
    days = days / 1000 / 60 / 60 / 24;
    return Math.floor(days) + 1;
}

// Считает количество недель с указанным днем за указанный период
function whatWeekDay(end, ...day) {
    let now = new Date().getTime();
    let count = 0;
    end = end.getTime();

    while (end >= now) {
        now += 1000 * 60 * 60 * 24;
        let date = stringData(now);
        if (day.includes(date.getDay())) {
            count++;
        }
    }

    return count;
}

// Считает количество месяцев с указанным днем за указанный период
function whatMounthDay(end, ...day) {
    let now = new Date().getTime();
    let count = 0;
    end = end.getTime();

    while (end >= now) {
        now += 1000 * 60 * 60 * 24;
        let date = stringData(now);
        if (day.includes(date.getDate())) {
            count++;
        }
    }

    return count;
}

// Меняет миллисекунды на объект даты
function stringData(ms) {
    let date = new Date(ms);
    date = new Date(date.toString());
    return date;
}